import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lineHeight: 1.5,
    isEditable: false,
    themeColor: '#293d52',
    showAvatar: true,
    isSquare: true,
    spaceX: 50,
  },
  mutations: {
    SET_LINE_HEIGHT(state, value) {
      state.lineHeight = value
    },
    SET_IS_EDITABLE(state, value) {
      state.isEditable = value
    },
    SET_THEME_COLOR(state, value) {
      state.themeColor = value
    },
    SET_SHOW_AVATAR(state, value) {
      state.showAvatar = value
    },
    SET_IS_SQUARE(state, value) {
      state.isSquare = value
    },
    SET_SPACE_X(state, value) {
      state.spaceX = value
    },
  },
})
