<template>
  <div class="toolbar bg-light border-left no-print overflow-hidden">
    <p v-if="!isChrome" class="p-2 text-danger">
      <i class="fa mr-1 fa-exclamation-circle" />
      <span>请用最新版chrome浏览器编辑!</span>
    </p>
    <h5 class="p-2 mb-0">全局设置</h5>
    <div class="p-2 border-top border-bottom">
      <v-swatches
        v-model="_themeColor"
        swatch-size="28"
        :spacing-size="6"
        background-color="transparent"
        inline
      ></v-swatches>
      <b-input-group prepend="主题颜色" class="mb-2" size="sm">
        <b-form-input v-model="_themeColor" type="text" />
        <b-form-input v-model="_themeColor" type="color" />
      </b-input-group>
      <b-input-group prepend="整体行高" class="mb-2" size="sm">
        <b-form-spinbutton
          v-model="_lineHeight"
          :step="0.1"
          :min="1"
          :max="2"
        />
      </b-input-group>
      <b-input-group prepend="左右边距" size="sm">
        <b-form-spinbutton
          v-model="_spaceX"
          :formatter-fn="(v) => v + 'px'"
          :step="2"
          :min="40"
          :max="60"
        />
      </b-input-group>
      <!-- <b-input-group prepend="全局编辑" size="sm">
        <b-input-group-prepend is-text>
          <b-form-checkbox switch class="mr-n2">
            <span class="sr-only">Switch for following text input</span>
          </b-form-checkbox>
        </b-input-group-prepend>
      </b-input-group> -->
    </div>

    <h5 class="p-2 mb-0">头像设置</h5>
    <div class="p-2 border-top border-bottom d-flex">
      <b-form-checkbox v-model="_showAvatar" switch class="flex-grow-1">
        显示/隐藏
      </b-form-checkbox>
      <b-form-checkbox v-model="_isSquare" switch class="flex-grow-1">
        方形/圆形
      </b-form-checkbox>
    </div>

    <h5 class="p-2 mb-0">数据编辑</h5>
    <div class="p-2 border-top border-bottom">ss</div>

    <!-- <b-button size="sm">打印</b-button> -->
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import VSwatches from 'vue-swatches'
import 'vue-swatches/dist/vue-swatches.css'
import printHtml from '@/utils/print.js'
export default {
  name: 'Toolbar',
  // eslint-disable-next-line vue/no-unused-components
  components: { VSwatches },
  computed: {
    ...mapState([
      'lineHeight',
      'isEditable',
      'themeColor',
      'showAvatar',
      'isSquare',
      'spaceX',
    ]),
    isChrome() {
      return navigator.userAgent.toLowerCase().match(/chrome/)
    },
    _lineHeight: {
      get() {
        return this.lineHeight
      },
      set(value) {
        this.SET_LINE_HEIGHT(value)
      },
    },
    _isEditable: {
      get() {
        return this.isEditable
      },
      set(value) {
        this.SET_EDITABLE(value)
      },
    },
    _themeColor: {
      get() {
        return this.themeColor
      },
      set(value) {
        this.SET_THEME_COLOR(value)
        document.documentElement.style.setProperty('--primary', value)
      },
    },
    _showAvatar: {
      get() {
        return this.showAvatar
      },
      set(value) {
        this.SET_SHOW_AVATAR(value)
      },
    },
    _isSquare: {
      get() {
        return this.isSquare
      },
      set(value) {
        this.SET_IS_SQUARE(value)
      },
    },
    _spaceX: {
      get() {
        return this.spaceX
      },
      set(value) {
        this.SET_SPACE_X(value)
      },
    },
  },
  data() {
    return {
      checked: false,
      value: 1.6,
      selected: [],
      options: [
        { text: 'Red', value: 'red' },
        { text: 'Green', value: 'green' },
      ],
    }
  },
  methods: {
    ...mapMutations([
      'SET_LINE_HEIGHT',
      'SET_EDITABLE',
      'SET_THEME_COLOR',
      'SET_SHOW_AVATAR',
      'SET_IS_SQUARE',
      'SET_SPACE_X',
    ]),
    print() {
      console.log(this.$refs.printHtmlRef)
      let printData = this.$refs.printHtmlRef.outerHTML
      printHtml(printData)
    },
  },
}
</script>

<style lang="less" scoped></style>
